.showcase-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
}

.showcase-container h2 {
  font-weight: normal;
}

.grid-container {
  display: grid;
  padding: 2vw;
  justify-content: center;
  margin-top: 1rem;
  gap: max(2vh, 2vw);
  width: max(75%, 30rem);
  max-width: 80rem;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  grid-auto-rows: 1fr;
}

.grid-item {
  display: flex;
  background-color: white;
  box-shadow: 0px 0px 4px rgb(109, 109, 109);
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.grid-item-body {
  padding: 1rem 1.5rem 1rem 1.5rem;
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.grid-img {
  border-radius: 1rem 1rem 0 0rem;
  width: 100%;
  height: 14rem;
  object-fit: cover;
  transition: .5s ease;
  backface-visibility: hidden;
  opacity: 1;
  margin-bottom: 1rem;
}

.overlay-container {
  position: relative;
  width: 100%;
}


.overlay {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.overlay-container:hover .grid-img {
  opacity: 0.3;
}

.overlay-container:hover .overlay {
  opacity: 1;
}

.grid-buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-top: 3rem;
}

.grid-buttons a {
  color: inherit;
  display: flex;
  align-items: center;
  border-bottom: 2px solid transparent;
}

.grid-buttons span {
  margin-right: 0.25rem;
  font-size: 22px;
}

.grid-buttons a:hover {
  border-bottom: 2px solid black;
}